<template>
  <div class="content">
    <header class="header">
      <strong>明日菜谱</strong><span>{{ tomorrow }}</span>
    </header>
    <ul class="menu-wrapper">
      <li class="menu-box lunch">
        <div class="title">
          <Icon type="food2" :size="24" color="#ffffff" />
          <h3>午餐</h3>
        </div>
        <Menu :data="lunchData" />
      </li>
      <li class="menu-box dinner">
        <div class="title">
          <Icon type="food4" :size="24" color="#ffffff" />
          <h3>晚餐</h3>
        </div>
        <Menu :data="dinnerData" />
      </li>
    </ul>
  </div>
</template>
<script>
import Dayjs from 'dayjs'
import Menu from '@/components/Menu'
import Icon from '@/components/Icon'
import axios from 'axios'
import { apiHost } from '@/utils/config'
import { pickMenus } from '@/utils/data'
export default {
  components: {
    Menu,
    Icon
  },
  computed: {
    tomorrow: function () {
      return Dayjs()
        .add(1, 'day')
        .format('YYYY年MM月DD日')
    }
  },
  data () {
    return {
      lunchData: [],
      dinnerData: []
    }
  },
  mounted () {
    const tomorrow = Dayjs().add(1, 'day').format('YYYY-MM-DD')
    this.getLunch(tomorrow)
    this.getDinner(tomorrow)
  },
  methods: {
    getLunch (date) {
      axios
        .get(`${apiHost}/dinner/a/wechat/api/lunch/lunch`, {
          params: {
            startSearchDay: date,
            endSearchDay: date
          }
        })
        .then(res => {
          this.lunchData = res.data.body.list[0] ? pickMenus(res.data.body.list[0].lunchContent) : []
        })
        .catch(err => {
          console.log(err)
        })
    },
    getDinner (date) {
      axios
        .get(`${apiHost}/dinner/a/wechat/api/lunch/dinner`, {
          params: {
            startSearchDay: date,
            endSearchDay: date
          }
        })
        .then(res => {
          this.dinnerData = res.data.body.list[0] ? pickMenus(res.data.body.list[0].lunchContent) : []
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>
